"use client";

import { useEffect, useState } from "react";
import { Button } from "@/components/ui/button";
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { collection, getDocs, addDoc } from "firebase/firestore";
import { db } from "@/lib/firebase";

interface Mechanic {
  id: string;
  nome: string;
  cpf: string;
  telefone: string;
  especialidade: string;
  status: "ativo" | "inativo";
}

export default function MecanicosPage() {
  const [mechanics, setMechanics] = useState<Mechanic[]>([]);
  const [newMechanic, setNewMechanic] = useState({
    nome: "",
    cpf: "",
    telefone: "",
    especialidade: "",
  });
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  useEffect(() => {
    const fetchMechanics = async () => {
      try {
        const mechanicsSnap = await getDocs(collection(db, "MecanicoOperador"));
        const mechanicsData = mechanicsSnap.docs.map(doc => ({
          id: doc.id,
          ...doc.data(),
        })) as Mechanic[];
        setMechanics(mechanicsData);
      } catch (error) {
        console.error("Error fetching mechanics:", error);
      }
    };

    fetchMechanics();
  }, []);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    try {
      const docRef = await addDoc(collection(db, "MecanicoOperador"), {
        ...newMechanic,
        status: "ativo",
      });

      setMechanics([...mechanics, {
        id: docRef.id,
        ...newMechanic,
        status: "ativo",
      }]);

      setNewMechanic({
        nome: "",
        cpf: "",
        telefone: "",
        especialidade: "",
      });
      setIsDialogOpen(false);
    } catch (error) {
      console.error("Error adding mechanic:", error);
    }
  };

  return (
    <div className="space-y-6">
      <div className="flex justify-between items-center">
        <h1 className="text-2xl font-bold">Mecânicos e Operadores</h1>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button className="bg-black text-white hover:bg-gray-800">
              + Novo Mecânico/Operador
            </Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Novo Mecânico/Operador</DialogTitle>
            </DialogHeader>
            <form onSubmit={handleSubmit} className="space-y-4">
              <div>
                <Input
                  placeholder="Nome"
                  value={newMechanic.nome}
                  onChange={(e) => setNewMechanic({
                    ...newMechanic,
                    nome: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  placeholder="CPF"
                  value={newMechanic.cpf}
                  onChange={(e) => setNewMechanic({
                    ...newMechanic,
                    cpf: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  placeholder="Telefone"
                  value={newMechanic.telefone}
                  onChange={(e) => setNewMechanic({
                    ...newMechanic,
                    telefone: e.target.value,
                  })}
                  required
                />
              </div>
              <div>
                <Input
                  placeholder="Especialidade"
                  value={newMechanic.especialidade}
                  onChange={(e) => setNewMechanic({
                    ...newMechanic,
                    especialidade: e.target.value,
                  })}
                  required
                />
              </div>
              <Button type="submit" className="w-full bg-black text-white hover:bg-gray-800">
                Salvar
              </Button>
            </form>
          </DialogContent>
        </Dialog>
      </div>

      <div className="grid grid-cols-2 gap-6">
        {mechanics.map((mechanic) => (
          <div
            key={mechanic.id}
            className="bg-white rounded-lg shadow p-6"
          >
            <div className="flex justify-between items-start">
              <div className="flex items-start space-x-4">
                <div className="h-10 w-10 rounded-full bg-gray-200 flex items-center justify-center">
                  <span className="text-lg font-medium text-gray-600">
                    {mechanic.nome.charAt(0).toUpperCase()}
                  </span>
                </div>
                <div>
                  <h3 className="font-medium">{mechanic.nome}</h3>
                  <p className="text-sm text-gray-500">
                    CPF: {mechanic.cpf}
                  </p>
                  <p className="text-sm text-gray-500">
                    Telefone: {mechanic.telefone}
                  </p>
                  <p className="text-sm text-gray-500">
                    Especialidade: {mechanic.especialidade}
                  </p>
                </div>
              </div>

              <div className="flex items-center space-x-2">
                <span className={`px-3 py-1 rounded-full text-sm ${
                  mechanic.status === "ativo"
                    ? "bg-green-100 text-green-800"
                    : "bg-red-100 text-red-800"
                }`}>
                  {mechanic.status === "ativo" ? "Ativo" : "Inativo"}
                </span>
                <Button variant="ghost" size="sm">
                  <svg
                    xmlns="http://www.w3.org/2000/svg"
                    width="20"
                    height="20"
                    viewBox="0 0 24 24"
                    fill="none"
                    stroke="currentColor"
                    strokeWidth="2"
                    strokeLinecap="round"
                    strokeLinejoin="round"
                  >
                    <path d="M17 3a2.85 2.83 0 1 1 4 4L7.5 20.5 2 22l1.5-5.5L17 3Z" />
                  </svg>
                </Button>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
